import React, { Component } from 'react';
import "./Hub.css";
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';

const baseapi = 'http://' + window.location.hostname + ':1337';
const url = baseapi + "/zones";

export default class Zones extends Component {
  constructor() {
    super();
    this.state = {
      name: '',
      sensor: '',
      should: '',
      zone: '',
      dsm: '',
      away: '',
      max: '',
      min: '',
      is: '',
      measured: '',
      items: []
    }
  }

  componentDidMount() {
    fetch(url)
    .then(response => {
      console.log("response", response);
      return response.json();
    })
    .then(result => {
      console.log(result, result[0].measured);
      this.setState({items: result});
      this.setState({measured: result[0].measured});
    })
  }

  render() {
    let items = this.state.items;
    return (
      <div className="zones-page">
          <h1>Mätpunkter</h1>
          <p>Senaste mätning: {this.state.measured}</p>
          <div>
            {items.map(item => {
                let tempis = item.tempis.toFixed(1);
                let divided = tempis.toString();
                tempis = divided.replace(".", ",");
                return <span key={item.zone}>
                <Link to={`/zone/${ item.id }`} className="btn btn-block"><Button variant="light" size="lg" block><span className="tempis">{tempis} &deg;C</span><span>{item.name}</span> <span className="should">{item.should} &deg;C</span></Button></Link></span>
            }
            )}
          </div>
      </div>
    );
  }
}
